import addYears from "date-fns/addYears";
import endOfDay from "date-fns/endOfDay";
import lastDayOfMonth from "date-fns/lastDayOfMonth";
import lastDayOfQuarter from "date-fns/lastDayOfQuarter";
import startOfDay from "date-fns/startOfDay";
import startOfMonth from "date-fns/startOfMonth";
import startOfQuarter from "date-fns/startOfQuarter";
import startOfYear from "date-fns/startOfYear";
import subDays from "date-fns/subDays";
import subMonths from "date-fns/subMonths";
import subQuarters from "date-fns/subQuarters";
import subYears from "date-fns/subYears";

import { Shortcut } from "./DatePicker.types";

export const getDefaultDates = () => ({
	defaultMinDate: subYears(new Date().setHours(0, 0, 0, 0), 5),
	defaultMaxDate: addYears(new Date().setHours(0, 0, 0, 0), 5),
});

export const today = startOfDay(new Date());

/**
 * NOTE: If a change is made here, make sure it gets replicated in `__mocks__/DatePicker.defaults`
 */
export const defaultShortcutsList = {
	today: {
		label: "Today",
		getDates: () => [today, endOfDay(today)],
		id: "today",
	},
	yesterday: {
		label: "Yesterday",
		getDates: () => [subDays(today, 1), endOfDay(subDays(today, 1))],
		id: "yesterday",
	},
	lastSevenDays: {
		label: "Last 7 days",
		getDates: () => [subDays(today, 7), subDays(today, 1)],
		id: "last-seven-days",
	},
	lastThirtyDays: {
		label: "Last 30 days",
		getDates: () => [subDays(today, 30), subDays(today, 1)],
		id: "last-thirty-days",
	},
	lastNinetyDays: {
		label: "Last 90 days",
		getDates: () => [subDays(today, 90), subDays(today, 1)],
		id: "last-ninety-days",
	},
	lastMonth: {
		label: "Last month",
		getDates: () => {
			const lastMonthStartDate = startOfMonth(subMonths(today, 1));

			const lastMonthEndDate = lastDayOfMonth(subMonths(today, 1));

			return [lastMonthStartDate, lastMonthEndDate];
		},
		id: "last-month",
	},
	lastQuarter: {
		label: "Last quarter",
		getDates: () => {
			const lastQuarterStartDate = startOfQuarter(subQuarters(today, 1));

			const lastQuarterEndDate = lastDayOfQuarter(subQuarters(today, 1));

			return [lastQuarterStartDate, lastQuarterEndDate];
		},
		id: "last-quarter",
	},
	yearToDate: {
		label: "Year to date",
		getDates: () => [startOfYear(today), today],
		id: "year-to-date",
	},
};

export const DEFAULT_SHORTCUTS: Shortcut[] = [
	defaultShortcutsList.today,
	defaultShortcutsList.yesterday,
	defaultShortcutsList.lastSevenDays,
	defaultShortcutsList.lastThirtyDays,
	defaultShortcutsList.lastNinetyDays,
	defaultShortcutsList.lastMonth,
	defaultShortcutsList.lastQuarter,
	defaultShortcutsList.yearToDate,
];
